import React from 'react';
import styles from './Project.module.css'

function Project(props) {

    return (
        <div className={styles.project} id={'projects'}>
            <div className={styles.imageProgect} style={props.style}>
                {/*<a className={styles.btnShow} href="">See code</a>*/}
                <div className={styles.projectImg}>
                    <div className={styles.projectButton}>
                        <button className={styles.btnShow} url={props.url}>See code</button>
                        <button className={styles.btnShow} url={props.url}>See demo</button>
                    </div>
                </div>

            <div className={styles.projectInfo}>
                <span className={styles.projectTitle}>{props.title}</span>
                <span className={styles.description}>{props.description}</span>
            </div>
            </div>
        </div>

    );
}

export default Project;
